body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFBE0B;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.addUsers-wrapper {
  display: flex;
  flex-direction: column;
  width: 20%;
  margin: auto;
  text-align: center; }
  .addUsers-wrapper h2 {
    text-transform: uppercase;
    color: #161925; }
  .addUsers-wrapper button {
    display: flex;
    text-align: center;
    width: 20%;
    margin: 1% auto;
    height: 30px;
    width: 50px;
    background-color: #161925;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    text-transform: uppercase; }

.user {
  flex-basis: 25%;
  min-width: 240px;
  background-color: #161925;
  color: white;
  margin: 1%;
  padding: 1%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .user img {
    border-radius: 50%;
    border: 5px solid white;
    width: 30%; }
  .user h2 {
    text-transform: capitalize; }
  .user .location {
    text-transform: capitalize; }
  .user span {
    margin: 2%; }

.listOfUsers-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  background-color: #FFBE0B;
  justify-content: center; }

.more-clear {
  width: 20%;
  display: flex;
  justify-content: space-around;
  flex-flow: wrap row;
  margin: auto;
  margin-bottom: 1%; }
  .more-clear button {
    display: flex;
    text-align: center;
    width: 20%;
    margin: 1% auto;
    height: 30px;
    width: 100px;
    background-color: #161925;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    text-transform: uppercase; }
    .more-clear button span {
      margin: auto; }

.header {
  width: 100%;
  height: 50vh;
  background: url(/static/media/creative_team.9a4854f8.png) no-repeat;
  background-size: contain;
  background-position: center; }


